import { ADD_MESSAGE, addMessage } from '@actions/messageActions';

export default store => next => action => {
    switch (action.type) {
        case ADD_MESSAGE:
            if (action.sender === '') {
                const timeout = setTimeout(() => {
                    store.dispatch(addMessage('  !', '', action.chatId, true));
                    clearTimeout(timeout);
                }, 1000);
            }
    }
    return next(action);
};